//Breakpoints
//Points from foundation

@mixin smakk-breakpoint($point) {
  @if $point == large {
  @media (min-width: 64em) { @content ; }
  } @else if $point == medium {
  @media (min-width: 40em) { @content ; }
  } @else if $point == small {
  @media (max-width: 40em) { @content ; }
  }
}


//Display mixins
@mixin smakk-display-flex($direction: row, $wrap: nowrap) {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  flex-direction: $direction;
  flex-wrap: $wrap
}

@mixin smakk-flex-direction($value: row) {
  @if $value==row-reverse {
    -webkit-box-direction: reverse;
    -webkit-box-orient: horizontal;
  }
  @else if $value==column {
    -webkit-box-direction: normal;
    -webkit-box-orient: vertical;
  }
  @else if $value==column-reverse {
    -webkit-box-direction: reverse;
    -webkit-box-orient: vertical;
  }
  @else {
    -webkit-box-direction: normal;
    -webkit-box-orient: horizontal;
  }
  -webkit-flex-direction: $value;
  -moz-flex-direction: $value;
  -ms-flex-direction: $value;
  flex-direction: $value;
}

//Styles
@mixin smakk-borders($border-w, $border-s, $border-c) {
  border: $border-w $border-s $border-c
}

@mixin smakk-box-shadow(){
  box-shadow: 0 14px 10px -10px  rgba(0,0,0,.2);
}

@mixin smakk-inline-list() {
  list-style-type: none;
  display: inline-block;
  margin: 0;
  padding: 0
}

@mixin smakk-background-cover() {
  background-repeat: no-repeat;
  background-position: center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
//Fonts and Weights

//Fluid typography

$mod_1: .4;
$mod_2: 1;


// Fluid Type Mixin
// https://codepen.io/anon/pen/RZxvMm
// Calculate Modular Scale

$h1-min: $mod_1 * $scale-7;
$h1-max: $mod_2 * $scale-7;

$h2-min: $mod_1 * $scale-7;
$h2-max: $mod_2 * $scale-6;

$h3-min: $mod_1 * $scale-6;
$h3-max: $mod_2 * $scale-5;

$h4-min: $mod_1 * $scale-5;
$h4-max: $mod_2 * $scale-4;

$h5-min: $mod_1 * $scale-4;
$h5-max: $mod_2 * $scale-3;

$h6-min: $mod_1 * $scale-3;
$h6-max: $mod_2 * $scale-2;

@function strip-unit($value) {
  @return $value / ($value * 0 + 1);
}

@mixin smakk-fluid-type($min-vw, $max-vw, $min-font-size, $max-font-size) {
  $u1: unit($min-vw);
  $u2: unit($max-vw);
  $u3: unit($min-font-size);
  $u4: unit($max-font-size);

  @if $u1 == $u2 and $u1 == $u3 and $u1 == $u4 {
  & {
    font-size: $min-font-size;
    @media screen and (min-width: $min-vw) {
    font-size: calc(#{$min-font-size} + #{strip-unit($max-font-size - $min-font-size)} * ((100vw - #{$min-vw}) / #{strip-unit($max-vw - $min-vw)}));
    }

    @media screen and (min-width: $max-vw) {
    font-size: $max-font-size;
    }
  }
  } @else {
  @error "Detected mixed units. Please use the same units for all parameters. " + $u1 +", " + $u2 + ", " + $u3 +", "+ $u4;
  }
}

@mixin primary-font {
  font-family: $font-primary;
  font-weight: $weight-roman;

  @supports (font-variation-settings: normal) {
    font-family: $font-primary-var;
  }
}

@mixin primary-font-bold {
  font-family: $font-primary;
  font-weight: $weight-bold;

  @supports (font-variation-settings: normal) {
    font-family: $font-primary-var;
  }
}

//Nav links
@mixin smakk-nav-links ($color, $font-size){
  color: $color;
  font-size: $font-size;
  a {
    color: $color;
    text-decoration: none;
    border-bottom: none;
    font-weight: bold;

    &:hover {
      border-bottom: none
    }
  }
}

//BUTTONS, TOGGLES
/// Colors for toggles.
/// @type Map
$toggle-colors: ( is-green: $c-green, is-white: #fff, is-blue: $c-blue) !default;
@mixin smakk-toggle-item($w, $h) {
  width: $w;
  height: $h;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0);
  border: 2px solid;
  &:hover,
  &.is-active {
    background-color: map-get($toggle-colors, default);
     border-color: map-get($toggle-colors, default);
    @each $color,
    $value in map-remove($toggle-colors, default) {
      &.#{$color} {
        background-color: $value;
        border-color: $value
      }
    }
  }
}
